import { StreetViewPage } from './streetview';
import { DatePipe } from '@angular/common';
import { VelocidadePipe } from './../../pipes/velocidade.pipe';
import { DistanciaPipe } from './../../pipes/distancia.pipe';
import { NgModule } from '@angular/core';
import { TranslateModule } from '@ngx-translate/core';
import { IonicPageModule } from 'ionic-angular';


@NgModule({
  declarations: [
    StreetViewPage,
  ],
  imports: [
    IonicPageModule.forChild(StreetViewPage),
    TranslateModule.forChild()
  ],
  exports: [
    StreetViewPage
  ],
  providers: [
    DistanciaPipe, VelocidadePipe, DatePipe
  ]
})
export class StreetViewPageModule { }



