import { DataStore } from './../../models/data-store';
import { Rastro } from './../../models/rastro';
import { Marcador } from "./../../models/marcador";
import { LoginPage } from "./../index";
import { Component, ElementRef, ViewChild, OnInit } from "@angular/core";
import { FormBuilder, FormGroup } from "@angular/forms";
import { TranslateService } from "@ngx-translate/core";
import {
  IonicPage,
  NavController,
  NavParams,
  Platform,
  LoadingController
} from "ionic-angular";

import { Settings, User } from "../../providers";
import { MyApp } from "../../app/app.component";
import { VeiculoService } from "../../providers/api/veiculo.service";
import {
  GoogleMaps,
  GoogleMap,
  LatLng,
  GoogleMapsEvent,
  Marker,
  MarkerOptions,
  MarkerIcon,
  GoogleMapsMapTypeId,
  MapType,
  GoogleMapOptions,
  HtmlInfoWindow,
  StreetViewPanorama
} from "@ionic-native/google-maps";
import { VelocidadePipe } from '../../pipes/velocidade.pipe';
import { DatePipe } from '@angular/common';
import { DistanciaPipe } from '../../pipes/distancia.pipe';


@IonicPage()
@Component({
  selector: "page-mapa",
  templateUrl: "streetview.html"
})
export class StreetViewPage implements OnInit {
  target:any;
  @ViewChild("street") streetElement: ElementRef;
  constructor(public navParams: NavParams) {

  }

  ngOnInit(): void {
    this.target = this.navParams.get("target");
    if (this.target != null){
      let panorama: StreetViewPanorama = GoogleMaps.createPanorama(this.streetElement.nativeElement);

      panorama.setPosition({
        target: this.target
      });
    }
  }
}



